<template>
    <div style="max-height: 90vh; overflow: auto;" class="box">
        <div style="background-color: #121212; display: flex; flex-direction: column; box-sizing: border-box;" v-loading="loading" element-loading-text="加载中...">
            <div v-for="everyNews in newsList" :key="everyNews" class="bar">
                <a :href="'https://game.ero-labs.website/cn/news.html?id=' + everyNews.notice_id" style="text-decoration: none;" target="_blank">
                    <div class="skeleton" v-if="!everyNews.loaded">
                        <el-skeleton-item variant="image" style="width: 100%; height: 50px" />
                    </div>
                    <img :src="everyNews.banner" style="width: 100%; border-radius: 8px;" @load="everyNews.loaded = true">
                    <span style="font-size: 12px; color: #f0ece5;">{{ everyNews.name }}</span>
                    <br>
                    <span style="font-size: 12px; background-color: #f0ece5; display: inline-block; margin-bottom: 8px; padding: 0 6px; border-radius: 10px; color: #181d22;">{{ everyNews.start_date }}</span>
                </a>
            </div>

        </div>
    </div>
</template>



<script>
import axios from 'axios'
import newsData from '../assets/json/newsData.json';

export default {
    data(){
        return{
            newsList:[],
            loading: true,
        }
    },
    mounted(){
        this.catchNews()
    },
    methods:{
        catchNews(){
            axios({
                method: 'POST',
                url: '/api/api/getNoticeList?hgame_id=47&lang=cn',
                headers: {
                    "Accept-Language": "zh-CN,zh;q=0.9",
                },
            })
            .then((res)=>{
                this.newsList = res.data.data
                this.loading = false
            })
            .catch(()=>{
                this.newsList = newsData.data
                this.loading = false
            })
        }
    }
}
</script>


<style scoped>
.bar{
    cursor: pointer;
    transition: .5s ease;
    opacity: .8;
}
.bar:hover{
    opacity: 1;
}
.box::-webkit-scrollbar {
    width: 2px;
}
.box::-webkit-scrollbar-thumb {
    border-radius: 12px;
    background: transparent;
}
.box::-webkit-scrollbar-track {
    border-radius: 12px;
    background: transparent;
}
</style>
