<template>
    <div style="display: inline-block;">
        <div class="upOrDown_box">
            <div
                v-for="item, index in attr"
                :key="item"
                :style="{ 'background-color': attr_color[index]}"
                class="upOrDown_boxInside"
                @click="click(index)"
            >
                {{ item }}
                <ArrowDown class="upOrDown_icon" :style="{ 'display': attr_display[index] , 'transform': `rotateX(${ attr_downOrUp[index] === 0 ? 0 : 180 }deg)`,'transition':'.3s ease'}"/>
            </div>
        </div>
    </div>
</template>


<script>
export default{
    props:['characterData_upOrDown'],
    data(){
        return{
            attr:['默认','HP','ATK','MATK','DEF','MDEF','SPD','MIND'],
            attr_display:['block','none','none','none','none','none','none','none'],
            attr_color:['#d12a2f','#7e282b','#7e282b','#7e282b','#7e282b','#7e282b','#7e282b','#7e282b'],
            attr_downOrUp:[0,0,0,0,0,0,0,0],

            // 什么属性被选中
            clickedAttr_name:-1,
            // 正序还是倒叙
            clickedAttr_upOrDown:-1
        }
    },
    methods:{
        click(num){
            if( this.attr_color[num] === '#d12a2f'){
                this.attr_downOrUp = this.attr_downOrUp.map((item, index) => {
                    if( index === num )return item === 0 ? 1 : 0;
                    else return item = 0
                });
            }else{
                this.attr_downOrUp = this.attr_downOrUp.map(() => {
                    return 0;
                });
            }
            this.attr_display = this.attr_display.map((_, index) => {
                return index === num ? 'block' : 'none';
            });
            this.attr_color = this.attr_color.map((_, index) => {
                return index === num ? '#d12a2f' : '#7e282b';
            });
            
            this.clickedAttr_name = num-1
            this.clickedAttr_upOrDown = this.attr_downOrUp[num]
            this.characterData_upOrDown(this.clickedAttr_upOrDown,this.clickedAttr_name)
            this.$emit('onEmitIndex_uod', {'name':this.clickedAttr_name, 'uod':this.clickedAttr_upOrDown})
        },
    }
}
</script>



<style scoped>
.upOrDown_box{
    background-color: rgb(71, 71, 71);
    display: flex;
    border-radius: 20px;
    overflow: hidden;
    margin-bottom: 10px;
}
.upOrDown_boxInside{
    display: flex;
    align-items: center;
    padding: 8px 12px;
    text-align: center;
    color: aliceblue;
    font-size: 14px;
    cursor: pointer;
    user-select: none;
    transition: .3s;
}
.upOrDown_icon{
    width: 15px;
    padding: 0 5px;
}
</style>