<template>
    <div style="margin-top: 10px; border-bottom: 1px solid #6e70746f; padding-bottom: 8px; margin-bottom: 8px;">
        <div>
            <div style="display: flex;">
                <div style="display: inline-block; border: 1px solid #2f3d5d; border-radius: 15px; overflow: hidden; height: 23px;">
                    <span style="font-size: 14px; padding: 2px 10px;">爱用武器</span>
                    <span style="background-color: #2f3d5d; color: #fef8ef; padding: 2px 10px; font-size: 14px;">{{ character.ownWeapon.name }}</span>
                </div>
                <span class="narmalSkill">
                    <img :src="require(`@/assets/icon/others/el_${character.ownWeapon.element}.png`)">
                    <img :src="require(`@/assets/icon/others/wp_${character.ownWeapon.weapon}.png`)">
                </span>
            </div>
            <div style="padding-left: 15px; margin-top: 2px;">
                <span style="font-size: 12px; margin-right: 30px;">使用次数 <b>{{ character.ownWeapon.times }}次</b></span>
                <span style="font-size: 12px;">神绊值 <b>{{ character.ownWeapon.upValue }}</b></span>
            </div>
            <div style="padding-left: 15px;">
                <span style="font-size: 12px; margin-right: 30px;">攻击次数 <b>{{ character.ownWeapon.heat }}连击</b></span>
                <span style="font-size: 12px;">范围 <b>{{ character.ownWeapon.scope }}</b></span>
            </div>
            <div style="padding-left: 15px; margin-top: 5px; font-size: 14px; border-radius: 5px; line-height: 22px;" :style="{'border-left':`${colorBox[character.ownWeapon.element ]} 4px solid`}">
                <span style="opacity: .7; font-size: 13px;">{{ character.ownWeapon.isatk === 1?'攻击':'' }} / </span>
                <span style="opacity: .7; font-size: 13px;">{{ character.ownWeapon.distance === 2?'远距离': character.ownWeapon.distance === 1?'中距离':character.ownWeapon.distance === 0?'近距离':''}} / </span>
                <span style="opacity: .7; font-size: 13px;">{{ character.ownWeapon.isall === 1?'全体':character.ownWeapon.isall === 0?'单体':character.ownWeapon.isall === 2?'纵列':character.ownWeapon.isall === 3?'横列':character.ownWeapon.isall === 4?'随机2人':'' }} / </span>
                <span style="opacity: .7; font-size: 13px;">{{ character.ownWeapon.isMagic === 1?'魔法':'物理' }}</span>
                <br>
                {{ character.ownWeapon.describe }}
            </div>
            <AttrShower :valueV="character.ownWeapon.panel" :valueName="weaponValueName"/>
        </div>
    </div>
</template>


<script>
import AttrShower from './AttrShower'
export default {
    props:['character','weaponValueName'],
    data(){
        return{
            colorBox:{
                "f":"#00ab2b",
                "h":"#b70000",
                "s":"#0a7dff",
                "y":"#a58632",
                "g":"#ffd009",
                "a":"#c70cff"
            }
        }
    },
    components:{
        AttrShower
    }
}
</script>


<style scoped>
.narmalSkill{
    margin-left: 6px;
    display: flex;
}
.narmalSkill img{
    height: 22px;
    margin-right: 5px;
}
</style>