                    <span style="font-size: 14px; padding: 2px 10px;">使用能力 <span style="opacity: .7; font-size: 12px;">· {{ index === 0 ? '其一' : index === 1 ? '其二' : '其三' }}</span></span>
                    <span style="background-color: #2f3d5d; color: #fef8ef; padding: 3px 10px; font-size: 14px;">{{ character.skill.normal.name[index-1] }}</span>
                </div>
                <span class="narmalSkill">
                    <img :src="require(`@/assets/icon/others/el_${character.skill.normal.element}.png`)">
                    <img :src="require(`@/assets/icon/others/wp_${character.skill.normal.weapon}.png`)">
                </span>
            </div>
            <div style="padding-left: 15px; margin-top: 2px;">
                <span style="font-size: 12px; margin-right: 30px;">发动几率 <b>{{ character.skill.normal.probability[index-1] }}</b></span>
            </div>
            <div style="padding-left: 15px; margin-top: 5px; font-size: 14px; border-radius: 5px; line-height: 22px;" :style="{'border-left':`${colorBox[character.skill.normal.element ]} 4px solid`}">
                <span style="opacity: .7; font-size: 13px;">{{ character.skill.normal.isatk === 1?'攻击':'' }} / </span>
                <span style="opacity: .7; font-size: 13px;">{{ character.skill.normal.scope[index-1] }} / </span>
                <span style="opacity: .7; font-size: 13px;">{{ character.skill.normal.isMagic === 1?'魔法':'物理' }}</span>
                <br>
                {{ character.skill.normal.describe[index-1] }}
            </div>
        </div>
    </div>
</template>


<script>
export default {
    props:['character'],
    data(){
        return{
            colorBox:{
                "f":"#00ab2b",
                "h":"#b70000",
                "s":"#0a7dff",
                "y":"#a58632",
                "g":"#ffd009",
                "a":"#c70cff"
            }
        }
    }
}
</script>


<style scoped>
.narmalSkill{
    margin-left: 6px;
    display: flex;
}
.narmalSkill img{
    height: 22px;
    margin-right: 5px;
}
</style>