<template>
    <div style="width: 200px; height: 90vh; position: absolute; background-color: #121212; padding-bottom: 20px;">
        <div style="width: 180px;">
            <div style="width: 155px; margin: 0 auto; max-height: 90vh; overflow: hidden; z-index: 2; position: relative; border-radius: 10px;" class="newsBar">
                <NewsWindow/>
            </div>
        </div>
        <span style="background-color: #f0ece5; height: 100%;
        width: 20px; position: absolute; right: 0; bottom: 0; border-radius: 20px 0 0 0;"></span>
        <span style="background-color: #121212; height: 100%;
        width: 20px; position: absolute; right: 20px; bottom: 0; border-radius: 0 0 20px 0; z-index: 1;"></span>
        <span style="background-color: #f0ece5; height: 100%;
        width: 20px; position: absolute; right: 20px; bottom: 0; z-index: 0;"></span>
    </div>
    <div class="BoxBox" :style="{'padding-right': drawer === false? '60px':'400px'}">
        <el-input v-model="searchName" placeholder="搜索角色名" @input="searchByName" style="width: 200px; margin: 0 40px 0 25px; height: 35px; box-shadow: #7e282b16 0 0 30px 1px; --el-input-focus-border-color: #ac2914; --el-input-border-radius: 15px;"/>
        <UpOrDownBar class="downOrUp" @onEmitIndex_uod="onEmitIndex_uod" :characterData_upOrDown="characterData_upOrDown"/>
        <div style="padding: 10px 25px; margin-top: 15px; background-color: #e7dbcf; border-radius: 15px;">
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="elements" :textName="'el'" :num="0" :kind="'元素'"/>
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="rarity" :textName="'text'" :num="1" :kind="'稀有度'"/>
            <CheckBarOnlyText @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="isMagicText" :num="2" :kind="'属性'"/>
            <CheckBarOnlyText @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="identityText" :num="4" :kind="'身份'"/>
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="weapon" :textName="'wp'" :num="3" :kind="'武器'"/>
        </div>
        <div style="margin-top: 12px; margin-left: 10px; font-size: 14px; opacity: .8; color: #314656;">
            <p><span style="padding: 0 10px 0 0; opacity: .3;">——</span>{{ characterWillShow.length }} / {{ characterData.length }}<span style="padding: 0 0 0 10px; opacity: .3;">——</span></p>
        </div>
        <div style="margin-top: 10px;">
            <CharacterItem
                v-for="(item, index) in characterWillShow"
                :key="index"
                :character="item"
                @click="showTable(index)"
                :upOrDown_name="upOrDown_name"
            />
        </div>
        <el-drawer
            v-model="drawer"
            :with-header="false"
            :modal="false"
            :close-on-click-modal="false"
            :lock-scroll="false"
            :size="380"
            :z-index="2"
            style="background-color: #fef8ef;"
        >
            <CharacterTable :character="tabling" :drawer="drawer"/>
        </el-drawer>
    </div>
</template>



<script>
import characterData from '../assets/json/characterData.json'
import CharacterTable from './CharacterTable'
import CharacterItem from './CharacterItem'
import CheckBar from './CheckBar'
import CheckBarOnlyText from './CheckBarOnlyText'
import UpOrDownBar from './UpOrDownBar'
import NewsWindow from './NewsWindow'

export default {
    data() {
        return {
            characterData:characterData,
            characterWillShow:[],

            searchName:'',
            characterAfterSearch:[],

            elements:['h','f','s','y','g','a'],
            rarity:['r','sr','ssr'],
            isMagicText:['物理','魔法'],
            identityText:['魂友','神器'],
            weapon:['c','f','ft','g','j','jd','q','s','y'],
            checkedElements:[[],[],[],[],[]],
            characterAfterChoise:[],

            upOrDown_name:-1,
            upOrDown_uod:-1,


            drawer:false,
            tabling:{},
        };
    },
    mounted(){
        this.checkedElements[0] = this.elements
        this.checkedElements[1] = this.rarity
        this.checkedElements[2] = this.isMagicText
        this.checkedElements[3] = this.weapon
        this.checkedElements[4] = this.identityText
        this.characterAfterSearch = this.characterData
        this.choise()
    },
    methods:{
        showTable(e){
            if( characterData[e].iconId === this.tabling.iconId || this.drawer === false){
                this.tabling = characterData[e]
                this.drawer = !this.drawer
            }else{
                this.tabling = characterData[e]
            }
        },
        choise(){
            this.characterAfterChoise = this.characterData.filter(item => {
                return (
                    this.checkedElements[0].includes(item.element)&&
                    this.checkedElements[1].includes(item.rarity)&&
                    this.checkedElements[2].includes((item.isMagic===1?'魔法':'物理'))&&
                    (this.checkedElements[3].includes(item.weapon[0])||this.checkedElements[3].includes(item.weapon[1]))&&
                    this.checkedElements[4].includes((item.isSQ===1?'神器':'魂友'))
                );
            });
            this.showCharacterAfterSearchAndChoise()
        },
        checkedElementsChange(newElements,elNum){
            this.checkedElements[elNum] = newElements
        },
        searchByName(){
            this.characterAfterSearch = this.searchName === '' ? this.characterData : this.characterData.filter( item => { return item.name_CN.includes(this.searchName)})
            this.showCharacterAfterSearchAndChoise()
        },
        showCharacterAfterSearchAndChoise(){
            this.characterWillShow = this.characterAfterChoise.filter(item1 => this.characterAfterSearch.some(item2 => item1.iconId === item2.iconId))
        },
        onEmitIndex_uod(e) {
            this.upOrDown_name = e['name']
            this.upOrDown_uod = e['uod']
        },
        characterData_upOrDown(upOrDown, attrNum){
            if( attrNum >= 0){
                upOrDown === 0 ? this.characterWillShow.sort((a, b) => b.attr[attrNum] - a.attr[attrNum]) : this.characterWillShow.sort((a, b) => a.attr[attrNum] - b.attr[attrNum])
            }else{
                upOrDown === 0 ? this.characterWillShow.sort((a, b) => parseInt(a.iconId) - parseInt(b.iconId)) : this.characterWillShow.sort((a, b) => parseInt(b.iconId) - parseInt(a.iconId))
            }
        },
    },
    components: {
        CharacterItem,
        CharacterTable,
        CheckBar,
        CheckBarOnlyText,
        UpOrDownBar,
        NewsWindow,
    }
}
</script>


<style scoped>
.BoxBox{
    overflow: auto;
    max-height: 90vh;
    transition: .5s ease;
    padding: 0 60px 0 210px;
    margin-top: 20px;
}
.downOrUp{
    display: inline-block;
}
@media (max-width: 1400px) {
    .downOrUp{
        margin-top: 25px;
        margin-left: 30px;
    }
}
.newsBar::-webkit-scrollbar {
    width: 2px;
}
.newsBar::-webkit-scrollbar-thumb {
    border-radius: 12px;
    background: transparent;
}
.newsBar::-webkit-scrollbar-track {
    border-radius: 12px;
    background: transparent;
}
</style>
