<template>
    <div class="btnBox">
        <el-tag type="warning" class="tag">{{ kind }}</el-tag>
        <el-checkbox
            v-model="isAllChecked"
            @change="handleCheckAllChange"
            class="checkbtn"
            style="background: linear-gradient(to bottom right, #9d2909, #d12a2f); width: 80px; border-radius: 20px;"
            border
        >
            <p style="position: absolute; top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%);">全选</p>
        </el-checkbox>
        <el-checkbox-group
            v-model="checkedElements"
            @change="handlecheckedElementsChange"
            style="border-bottom: 1px dotted #9f9f9f; padding: 2px 0 4px; margin: 2px 0 2px 15px;"
        >
            <el-checkbox v-for="element in elements" :key="element" :label="element" class="checkbtn" border style="background-color: #919e8b !important;background-color: #f0ece5 !important; background: linear-gradient(to bottom right, #ebe1d8, #f0ece5);">
                <p style="color: #2f3d5d;position: absolute; top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%); font-size: 13px;">{{ element }}</p>
            </el-checkbox>
        </el-checkbox-group>
    </div>
</template>



<script>
export default {
    props:['elements','num','kind'],
    data() {
        return {
            checkedElements:[],
            isAllChecked:true
        };
    },
    mounted(){
        this.checkedElements = this.elements
        this.$emit('checkedElementsChange',this.checkedElements)
    },
    methods:{
        handleCheckAllChange(){
            this.checkedElements.length === this.elements.length ? this.checkedElements = [] : this.checkedElements = this.elements
            this.$emit('checkedElementsChange',this.checkedElements,this.num)
            this.$emit('checkChange');
        },
        handlecheckedElementsChange(){
            this.checkedElements.length === this.elements.length ? this.isAllChecked = true : this.isAllChecked = false
            this.$emit('checkedElementsChange',this.checkedElements,this.num)
            this.$emit('checkChange');
        },
    },
}
</script>


<style scoped>
.checkbtn{
    line-height: 100%;
}
</style>
