<template>
    <div style="margin-top: 40px; color: #121212;transition: .5s ease;">
        <div style="position: relative; display: flex; align-items: center;">
            <CharacterItem :character="character" style="cursor: default;"/>
            <div style="display: inline-block; margin-left: 10px;">
                <p class="isSQ_ismagic">{{ character.isSQ === 0 ? '魂友':'神器'}} {{ character.isMagic === 1 ? '魔法' : '物理'}}</p>
                <h4 class="characterName">{{ character.name_CN }}</h4>
                <div class="imgBox" v-if="character.isSQ === 0">
                    <p>擅长武器</p>
                    <div style="background-color: #2f3d5d; width: 100%; text-align: center;">
                        <img :src="require(`@/assets/icon/others/wp_${character.weapon[0]}.png`)" >
                        <img :src="require(`@/assets/icon/others/wp_${character.weapon[1]}.png`)" >
                    </div>
                </div>
                <div class="imgBox" v-else>
                    <p>擅长武器</p>
                    <div style="background-color: #2f3d5d; width: 100%; text-align: center;">
                        <img :src="require(`@/assets/icon/others/wp_${character.weapon[0]}.png`)" >
                    </div>
                </div>
                <p class="painterName"><span class="textBar_painterName">角色设计</span>{{ character.painter }}</p>
            </div>
        </div>
        <div class="showPicBtn" @click="pictureShow=!pictureShow">
            <p style="display: flex; align-items: center; justify-content: center;"><DArrowLeft :style="{'height': '13px', 'margin-right': '5px', 'transition': '.3s ease', 'transform': `rotate(-90deg) rotateY(${ pictureShow === false ? 0 : 180 }deg)`}"/>{{ pictureShow === false ? "展示" : "收起"}}角色立绘</p>
        </div>
        <div style="width: 100%; transition: .5s ease;" :style="{'height': drawerValue === false ? '0px' : '400px'}">
            <div class="picBox">
                <el-image style="width: 95%; height: auto; margin: 0 auto;" :src="require(`@/assets/picture/character/${character.iconId}.png`)" fit="contain" @load="handleImageLoad" :style="{'display': imageLoaded === false ? 'none':'inline-block'}" :previewSrcList="[require(`@/assets/picture/character/${character.iconId}.png`)]" preview-teleported="true" hide-on-click-modal="true"/>
                <el-skeleton-item variant="image" style="width: 100%; height: 400px" :style="{'display': imageLoaded === true ? 'none':'inline-block'}"/>
            </div>
        </div>
        <!-- <el-drawer
            v-model="drawerValue"
            :modal="false"
            :close-on-click-modal="false"
            :lock-scroll="false"
            :z-index="1"
            :append-to-body="true"
            :before-close="handleClose"
            :size="'50vw'"
            style="background-color: #82bff2 ;padding-top: 50px;"
            :with-header="false"
        >
        <div class="picBox">
            <el-image style="width: 100%; height: auto" :src="require(`@/assets/picture/character/${character.iconId}.png`)" fit="contain" />
        </div>
        <div style="position: absolute; bottom: 10px; width: 23vw; text-align: center;">
            <img src="../assets/icon/others/logo_text.png" class="logo">
        </div>
        </el-drawer> -->
        <div style="border-bottom: 1px solid #6e70746f; padding-bottom: 8px;">
            <AttrShower :valueV="character.attr" :valueName="characterValueName"/>
        </div>
        <div v-if="character.isSQ === 0">
            <SkillNormal :character="character"/>
            <SkillUltimate :character="character"/>
            <SkillPassivity :character="character"/>
        </div>
        <div v-else>
            <HSkillUltimate :character="character"/>
            <HSkillNormal :character="character"/>
            <HSkillEffect :character="character"/>
            <HSkillPassivity :character="character"/>
        </div>
        <div v-if="character.isSQ === 0">
            <WeaponL :character="character" :weaponValueName="weaponValueName"/>
        </div>
        <div>
            <p style="font-size: 14px; padding: 0 5px; opacity: .85; margin-top: 15px;">{{ character.Introduction }}</p>
        </div>
    </div>
</template>



<script>
import CharacterItem from './CharacterItem'
import SkillNormal from './SkillNormal'
import SkillUltimate from './SkillUltimate'
import SkillPassivity from './SkillPassivity'
import AttrShower from './AttrShower'
import WeaponL from './WeaponL'
import HSkillUltimate from './HSkillUltimate'
import HSkillNormal from './HSkillNormal'
import HSkillEffect from './HSkillEffect'
import HSkillPassivity from './HSkillPassivity'

export default {
    props: ['character','drawer'],
    data(){
        return{
            pictureShow:false,
            characterValueName:['HP','ATK','MATK','DEF','MDEF','SPD','MIND'],
            weaponValueName:['HP','CRT率','ATK','MATK','DEF','MDEF','SPD','MIND'],
            imageLoaded: false,
        }
    },
    methods:{
        handleImageLoad() {
            this.imageLoaded = true;
        }
    },
    computed: {
        drawerValue() {
            return this.pictureShow && this.drawer;
        },
    },
    watch:{
        drawer(newValue){
            if(!newValue){
                this.pictureShow = false
            }
        },
    },
    components: {
        CharacterItem,
        SkillNormal,
        SkillUltimate,
        SkillPassivity,
        AttrShower,
        WeaponL,
        HSkillUltimate,
        HSkillNormal,
        HSkillEffect,
        HSkillPassivity,
    }
}
</script>


<style scoped>
.picBox{
    display: flex;
    align-items: center;
    overflow: hidden;
    position: relative;
    border-radius: 15px;
    background-color: #2b2b2b;
    background-image: url(../assets/character_bg.png);
    background-size: 100%;
    background-position: bottom;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}
.showPicBtn{
    cursor: pointer;
    transition: .3s;
    width: 100%;
    text-align: center;
    height: 20px;
    line-height: 20px;
    border-radius: 20px;
    font-size: 12px;
    color: #dd6b66;
    border: #dd6b66 1px solid;
    margin: 6px auto;
}
.showPicBtn:hover{
    background-color: #dd6c663a;
}
.isSQ_ismagic{
    font-size: 13px;
    opacity: .7;
}
.characterName{
    font-size: 23px;
    font-weight: 400;
}
.imgBox{
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
    right: 15px;
    top: 10px;
    border: #2f3d5d 2px solid;
    border-radius: 8px;
}
.imgBox p{
    font-size: 13.5px;
    padding: 2px 5px;
    color: #2f3d5d;
    font-weight: 600;
}
.imgBox img{
    padding: 2px 2px 0;
    height: 18px;
}
.painterName{
    font-size: 12px;
    line-height: 25px;
}
.textBar_painterName{
    background-color: #2f3d5d;
    color: #fef8ef;
    padding: 1px 8px;
    border-radius: 10px;
    margin-right: 5px;
}
</style>
