<template>
    <div class="btnBox">
        <el-tag type="warning" class="tag">{{ kind }}</el-tag>
        <el-checkbox
            v-model="isAllChecked"
            @change="handleCheckAllChange"
            class="checkbtn"
            style="background: linear-gradient(to bottom right, #9d2909, #d12a2f); width: 80px; border-radius: 20px;"
            border
        >
            <p style="position: absolute; top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%);">全选</p>
        </el-checkbox>
        <el-checkbox-group
            v-model="checkedElements"
            @change="handlecheckedElementsChange"
            style="border-bottom: 1px dotted #9f9f9f; padding: 2px 0; margin: 2px 0 2px 15px;"
        >
            <el-checkbox v-for="element in elements" :key="element" :label="element" class="checkbtn" border style="background-color: #f0ece5 !important; background: linear-gradient(to bottom right, #ebe1d8, #f0ece5);">
                <img :src="require(`@/assets/icon/others/${textName}_${element}.png`)">
            </el-checkbox>
        </el-checkbox-group>
    </div>
</template>



<script>
export default {
    props:['elements','textName','num','kind'],
    data() {
        return {
            checkedElements:[],
            isAllChecked:true
        };
    },
    mounted(){
        this.checkedElements = this.elements
        this.$emit('checkedElementsChange',this.checkedElements,this.num)
    },
    methods:{
        handleCheckAllChange(){
            this.checkedElements.length === this.elements.length ? this.checkedElements = [] : this.checkedElements = this.elements
            this.$emit('checkedElementsChange',this.checkedElements,this.num)
            this.$emit('checkChange');
        },
        handlecheckedElementsChange(){
            this.checkedElements.length === this.elements.length ? this.isAllChecked = true : this.isAllChecked = false
            this.$emit('checkedElementsChange',this.checkedElements,this.num)
            this.$emit('checkChange');
        },
    },
}
</script>


<style>
.btnBox{
    display: flex;
    align-items: center;
}
.tag{
    background-color: #314656 !important;
    color: #eeeeee !important;
    border: none !important;
    margin-right: 15px;
    width: 60px;
}
.checkbtn{
    cursor: pointer;
    margin: 5px;
}
.el-checkbox{
    display: inline-block;
    margin-right: 0 !important;
    background-color: #c6282d !important;
    text-align: center !important;
    width: 60px;
    height: 30px !important;
    border: 2px solid #fef8ef !important;
    opacity: .2;
    transition: .3s;
}
.is-checked{
    border-color: #f0ece5 !important;
    opacity: 1;
}
.el-checkbox__label{
    width: 100%;
    color: #f4f4f4 !important;
    padding: 0 !important;
}
.el-checkbox__inner{
    display: none !important;
}
.el-checkbox-button{
    --el-checkbox-button-checked-border-color: red
}
.el-checkbox-button.is-checked .el-checkbox-button__inner{
    background-color: #2f3d5d;
    border: 4px solid rgb(250, 34, 34);
}
.checkbtn img{
    height: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
</style>
